import { Component, OnInit } from '@angular/core';
import { Base } from '../../../model/base';
import { Router } from '@angular/router';
import { TipoInspecao } from './../../../model/objetc/tipoinspecao';
import { TipoinspecaoService } from './../../../service/sigobra/tipoinspecao.service';

@Component({
  selector: 'app-tipoinspecao-inserir',
  templateUrl: './tipoinspecao-inserir.page.html',
  styleUrls: ['./tipoinspecao-inserir.page.scss'],
})
export class TipoinspecaoInserirPage implements OnInit {
  tipoinspecao: TipoInspecao = new TipoInspecao();
  constructor(private tis: TipoinspecaoService, private base: Base, private router: Router, /*private apos: AnamneseprodutoService*/) { }

  ngOnInit() {
    if ((this.tis.tipoinspecao !== undefined) && (this.tis.tipoinspecao !== null)) {
      this.tipoinspecao = this.tis.tipoinspecao;
    }
  }

  get editando() {
    return Boolean(this.tipoinspecao.id)
  }

  salvar() {
    this.salvando();

  }

  salvando() {
    if ((this.tipoinspecao.ativo === undefined) || (this.tipoinspecao.ativo === null)) {
      this.tipoinspecao.ativo = true;
    }
    this.tipoinspecao.empresa = this.tis.as.token.empresa;
    this.base.present();
    this.tis.salvar(this.tipoinspecao).subscribe(
      data => {
        this.tis.tipoinspecao = new TipoInspecao();
        this.base.dismiss();
        this.router.navigate(['tipoinspecoes']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha ao salvar tipoinspecao :' + this.base.tratarErro(error));
      }
    );
  }

  /*itens tipo inspeção*/
  /*
   pesquisaAnamneseProduto(value: Anamnese) {
 
     return this.apos.byAnamnese(value).subscribe(
       data => {
 
         this.anamnese.anamneseproduto = data as Array<AnamneseProduto>;
       }, error => {
         this.anamnese.anamneseproduto = new Array<AnamneseProduto>();
 
         //this.base.mensagemErro('Falha  ao carregar ficha' + this.anamneseficha.id
         //+ this.base.tratarErro(error)); }
       }
     );
   }
 
 */
  itensti() {
    if ((this.tipoinspecao.ativo === undefined) || (this.tipoinspecao.ativo === null)) {
      this.tipoinspecao.ativo = true;
    }
    this.tipoinspecao.empresa = this.tis.as.token.empresa;
    this.tis.salvar(this.tipoinspecao).subscribe(
      data => {

        this.tis.tipoinspecao = data as TipoInspecao;
        this.router.navigate(['itenstipoinspecao']);
      }, error => {
        this.base.mensagemErro('Falha ao salvar tipo inspenção  :' + this.base.tratarErro(error));
      }
    );

  }

}
